"use client";

import { useState, useEffect } from "react";
import "./send-email.css";
import EmailBuilder from "./emailEditor";
import AuthGuard from "@/components/AuthGuard";

interface EmailData {
  id: string;
  to: string;
  subject: string;
  body: string;
  senderName: string;
}

interface SendEmailProps {
  emailId?: string;
}

export default function SendEmail({ emailId }: SendEmailProps) {

  const [to, setTo] = useState("");
  const [subject, setSubject] = useState("");
  const [body, setBody] = useState("");
  const [senderName, setSenderName] = useState("");

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState(false);

  // Load user safely
  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user") || "{}");
    setSenderName(user?.email || "");
  }, []);

  // Load existing email if editing
  useEffect(() => {
    if (!emailId) return;

    const fetchEmail = async () => {
      setLoading(true);
      try {
        const res = await fetch(`/api/emails/${emailId}`, {
          headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
        });
        const data: EmailData = await res.json();
        if (!res.ok)
          throw new Error(data?.toString() || "Failed to load email");

        setTo(data.to);
        setSubject(data.subject);
        setBody(data.body);
      // eslint-disable-next-line @typescript-eslint/no-explicit-any
      } catch (err: any) {
        setError(err.message || "Failed to fetch email");
      } finally {
        setLoading(false);
      }
    };

    fetchEmail();
  }, [emailId]);

  const sendOrUpdate = async () => {
    setError(null);
    setSuccess(false);

    if (!to || !subject || !body) {
      setError("All fields are required");
      return;
    }

    setLoading(true);

    try {
      const res = await fetch(
        emailId ? `/api/emails/${emailId}` : "/api/send",
        {
          method: emailId ? "PUT" : "POST",
          headers: {
            "Content-Type": "application/json",
            Authorization: `Bearer ${localStorage.getItem("token")}`,
          },
          body: JSON.stringify({ to, subject, body, senderName }),
        },
      );

      const data = await res.json();
      if (!res.ok) throw new Error(data.error || "Failed to send/update email");

      setSuccess(true);

      if (!emailId) {
        setTo("");
        setSubject("");
        setBody("");
      }
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    } catch (err: any) {
      setError(err.message || "Something went wrong");
    } finally {
      setLoading(false);
    }
  };

  return (
     <AuthGuard>
    <div className="container">
      <div className="card">
        {/* <button
          className="back-button"
          onClick={() => router.push("/crm/dashboard")}
        >
          ← Back
        </button> */}

        <h1>{emailId ? "Edit Email" : "Send Email"}</h1>
        <p className="subtitle">Track opens in real-time</p>

        {error && <div className="error">{error}</div>}
        {success && (
          <div className="success">
            {emailId
              ? "Email updated successfully!"
              : "Email sent successfully!"}
          </div>
        )}

        <label>To</label>
        <input
          type="email"
          placeholder="user@example.com"
          value={to}
          onChange={(e) => setTo(e.target.value)}
        />

        <label>Subject</label>
        <input
          placeholder="Email subject"
          value={subject}
          onChange={(e) => setSubject(e.target.value)}
        />

        <label>Message</label>
        {/* <textarea
          placeholder="Write your email here..."
          value={body}
          onChange={(e) => setBody(e.target.value)}
        /> */}

        <EmailBuilder body={body} setBody={setBody} />

        <button
          className="send-button"
          disabled={loading}
          onClick={sendOrUpdate}
        >
          {loading
            ? emailId
              ? "Updating…"
              : "Sending…"
            : emailId
              ? "Update Email"
              : "Send Email"}
        </button>
      </div>
    </div>
    </AuthGuard>
  );
  
}
